.flip-container {
perspective: 1000;
}
/* turning on hover */

.flip-container:hover .flipper {
transform: rotateY(180deg);
-webkit-transform: rotateY(180deg);
-webkit-transition-delay: 0s;
  -moz-transition-delay: 0s;
  -ms-transition-delay: 0s;
  -o-transition-delay: 0s;
  transition-delay: 0s;
}

.flip-container,
.front,
.back {
width: 100%;
height: 350px;
text-align: center;
font-size: 30px;
}
/* flipping speed here */

.front1 {
padding: 40px 30px;
background-image: url(https://www.planetware.com/photos-large/IND/india-top-attractions-taj-mahal.jpg);
width: 100%;
height: 350px;
